              </p>
            </template>
            <template v-else>
              <div class="card flex jc-center ai-center shadow-active">
                <span class="hdr-text-blue fs-16">NFT</span>
              </div>
              <p class="no-lv flex jc-between ai-center"></p>
            </template>
          </li>
        </ul>
        <div class="flex">
           
           
          <m-button v-if="isApproved" class="btn hdr-el-blue" @click="selectedIds=[];showSelection=true;getCardList();">
            <span>{{ language.btn_add_card }}</span>
          </m-button>

          <m-button v-else class="btn auth hdr-el-blue" :loading="authLoading" @click="setApproved()">
            <span>{{ language.btn_authorize }}</span>
          </m-button>

        </div>
        <div class="context flex1 flex ai-center">
          <ul class="flex jc-between ai-center text">
            <li>{{ language.lottery_no }}<br><b class="fs-20">NO. {{ market.epoch }}</b></li>
            <li class="ta-right">
              {{ language.end_clock }}<br>
              <b class="fs-20">{{ countDisplay }}</b>
            </li>
            <li class="pool flex ai-center">
              <img :src="coinMap.DFT" alt="DFT" class="icon-m">
              <p>{{language.tt_4_3}}<br><b class="fs-20">{{market.rewardAmount || '——'}}</b></p>
            </li>
          </ul>
        </div>

        <!--    选择并放置卡牌-->
        <m-popper v-model="showSelection">
          <template v-slot:title>{{ language.tt_4_2 }}</template>
          <div class="select-box ta-center">
            <ul class="wrap">
             
              <li class="select-item flex" :class="{active:selectedIds.indexOf(card.id)!==-1}"
                  v-for="card in myCardsList"
                  @click="selectCard(card.id)"
                  :key="card.id">
                <img class="card-img" :src="card.resUrl" alt="">
                <div class="c-info text animate-inout">
                  <b class="lv">Lv.{{ card.grade }}</b>
                  <b class="no">#{{ card.id }}</b>
                </div>
                <img class="icon" src="../../images/lottery/selected@2x.png" alt="">
              </li>
              <li v-if="bagLoading" class="mask flex jc-center ai-center"><span class="icon-loading"></span></li>
            </ul>
            <p class="tips flex jc-between">
              <span class="ta-left">* {{ language.tip_1_4 }}</span>
              <span class="ta-right">{{ language.tip_1_5 }}<b>{{ 5 - gameCards.length - selectedIds.length }}</b></span>
            </p>
            <m-button class="hdr-el-blue" @click="betNFT()" :loading="betLoading">
              <span>{{ language.btn_put_card }}</span>
            </m-button>
          </div>
        </m-popper>
        
      </div>
      <div class="box b2 flex1 flex flex-column p-25">
