              <li v-for="(rec) in currentRecords" :key="rec.id">
                <div v-if="rec.id" class="nft-item bg-radius flex jc-center ai-center relative"
                     :class="`reward-${rec.rewardLevel+1} ${!rec.claimed&&rec.rewardLevel>=0?'unclaimed':''}`"
                     @click="recordClaim(currentLottery,rec)"
                >
                  <div v-if="rec.rewardLevel<0" class="ta-center">
                    <p>{{ language.prize_0 }}</p>
                    <b class="text fs-18">#{{ rec.nft?.tokenId || '——' }}</b>
                  </div>
                  <div v-else class="flex ai-center">
                    <img class="icon-l" :src="prizeMap[rec.rewardLevel]" alt="">
                    <p v-if="rec.claimed">
                      {{ language.order_claimed }}<br>
                      <b class="fs-20">#{{rec.nft?.tokenId || '——' }}</b>
                    </p>
                    <p v-else>{{ language.reward }}(DFT)<br>
                      <b class="fs-20">{{ amountFormatter(rec.rewardAmount) }}</b>
                      <span class="btn-tip text absolute">{{language.order_unclaimed}}</span>
                    </p>
                  </div>
                </div>
                <div v-else class="nft-item bg-radius flex jc-center ai-center">
                  <b class="text fs-18">#——</b>
                </div>
              </li>
            </ul>
