<script>
import MButton from "@/components/mButton";
import MPopper from "@/components/mPopper";
import {nftGraphql} from "@/common/api";
import {mapActions, mapGetters, mapState} from "vuex";
import {BN, counter} from "@/common/util";
import LotteryReward from "@/views/nft/comps/lotteryReward";
import {coinMap, prizeMap} from "@/common/walletConfig";
import {dateFormatter} from "@/common/dataConvertor";

export default {
  name: "nftLottery",
  components: {LotteryReward, MPopper, MButton},
  data() {
    return {
      showQuery: false,
      showSelection: false,
      selectedIds: [],
      isApproved: false, //是否授权
      gameCards: [],
      prizeMap,
      coinMap,
      showList:false,
      betRecords: [],
      currentLottery: {
        records: []
      },
      timer:null,
      count:0,
      authLoading: false,
      bagLoading: false,
      betLoading: false,
      recordLoading: false,
      myCardsList:[],
    }
  },
  computed: {
    ...mapState('app', ['address']),
    ...mapState('option', ['currentBlock']),
    ...mapState('nft', ['myCards', 'market']),
    ...mapGetters(['language']),
    currentRecords() {
      return this.currentLottery.records;
    },
    countDisplay(){
      return counter.format(this.count);
    }
  },
  watch: {
    address() {
      this.checkIsApproved();
      this.getBetRecords();
    },
    market() {
      this.getBetRecords();
      this.startClock();
    },
    currentBlock(){
      this.startClock();
    }
  },
  methods: {
    ...mapActions('nft', ['deleteCard']),
    amountFormatter(amount){
      return new BN(amount).myFixed();
    },
    dateFormatter(str) {
      return str ? dateFormatter(str).format('yyyy-MM-dd') : '???-??-??'
    },
    startClock(){
      let {market,currentBlock}=this;
      this.count = counter.calClockCount(currentBlock, market.endBlock);
      clearInterval(this.timer);
      this.timer = setInterval(()=>this.count--,1000);
    },
    getBetRecords() {
      let {epoch} = this.market;
      if (!this.address || !epoch) return;
      if (this.recordLoading) return;
      let query = `query{
        bets(first: 200, orderBy:block, orderDirection:desc, where: {user: "${this.address}"}) {
          id
          rewardLevel
          rewardAmount
          startPoint
          endPoint
          claimed
          nft {
            tokenId
            grade
            quality
            resUrl
          }
          round {
            id
            epoch
            endBlock
            startAt
            endAt
          }
        }
      }`;
      this.recordLoading = true;
      nftGraphql(query).then(({bets}) => {
        let gameCards = [], betRecords = [];
        let r = {};
        bets.map((record) => {
          let {round, nft} = record;
          if (round.epoch === epoch) {
            gameCards.push(nft);
          } else {
            delete record['round'];
            // record.claimed = false;
            if (round.epoch !== r.epoch) {
              r = {};
              Object.assign(r, round, {records: [record]})
              betRecords.push(r);
            } else {
              r.records.push(record)
            }
          }
        });
        //结束加载
        this.recordLoading = false;
        this.gameCards = gameCards.reverse();
        this.currentLottery = betRecords[0] || {records: []};
        this.betRecords = betRecords;
      }).catch(() => {
        this.recordLoading = false;
      })
    },
    checkIsApproved() {
      if (!this.address) return;
      this.$api.checkIsApproved().then(res => this.isApproved = res)
    },
    setApproved() {
      
      this.authLoading = true;
      this.$api.setApprovalForAll().then(() => {
        this.authLoading = false;
        this.isApproved = true;
      }).catch(() => {
        this.authLoading = false;
      })
    },
    getCardList() {
      this.$api.gqlBagNftListAdd(this.address).then((res) => {
        this.myCardsList = res.defxTokens
      }).catch(() => {
      });
    },
    selectCard(id) {
      let idx = this.selectedIds.indexOf(id);
      let count = this.selectedIds.length + this.gameCards.length;
      idx === -1 ? count < 5 && this.selectedIds.push(id) : this.selectedIds.splice(idx, 1);
    },
    betNFT() {
      
      let betArr = this.selectedIds;
      if (betArr.length) {
        this.betLoading = true;
        this.$api.betNFT(betArr).then(() => {
          this.showSelection = false;
          this.betLoading = false;
          this.addCardsToGame();
          this.deleteCard(betArr);
        }).catch(() => {
          this.betLoading = false;
        })
      }
    },
    addCardsToGame() {
      let adds = this.selectedIds.map(id => this.myCards.find(c => c.id === id));
      this.gameCards = this.gameCards.concat(adds);
      
    },
    onSelect(round) {
      this.currentLottery = round;
    },
    recordClaim(round,rec){
      if (rec.claimed||rec.rewardLevel<0) return;
      let {nft} = rec;
      this.$api.claimNFT(round.epoch, nft.tokenId).then(() => {
        rec.claimed = true;
      }).catch(() => {

      })
    }
  },
  mounted() {
    this.checkIsApproved();
    this.getBetRecords();
  
  },
   beforeUnmount(){
      clearInterval(this.timer);// 清除定时器
      this.timer = null;
   },
}
</script>

