<template>
  <div class="lottery-reward flex">
    <div class="lottery relative">
      <img class="bg-1" src="../../../images/lottery/bg-1@2x.png" alt="">
      <div class="content text absolute">
        <div class="epoch p-25" @click="showList=!showList">
          <div class="no-row flex jc-between ai-center">
            <b class="fs-25">NO. {{ currentLottery.epoch || '——' }}</b>
            <img class="icon" src="../../../images/option/arrow_down@3x.png" alt="">
          </div>
          <p>{{ dateFormatter(currentLottery.startAt) }}</p>
          <ul class="select-list absolute" :class="{active:showList}">
            <li v-for="round in epochRounds" class="flex jc-between p-25"
                :class="{active:currentLottery.epoch===round.epoch}"
                @click="onSelect(round)"
                :key="round.epoch"
            >
              <b class="fs-16">NO. {{ round.epoch }}</b>
              <span>{{ dateFormatter(round.startAt) }}</span>
            </li>
          </ul>
        </div>
        <div class="pool">
          <p>{{ language.tt_4_3 }} (DFT)</p>
          <b class="fs-25">{{ currentLottery?.rewardAmount || '——' }}</b>
        </div>
      </div>
    </div>
    <ul class="reward-list flex1 flex">
      <li class="item" v-for="(tip,i) in tips" :class="'reward-'+(tip.level+1)" :key="i">
        <div class="tt flex jc-between ai-center">
          <div class="flex ai-center">
            <img class="icon-m" :src="prizeMap[tip.level]" alt="">
            <b class="fs-20">{{ levelFormatter(tip.level) }}</b>
          </div>
          <span>{{ language.label_1_2 }}:{{ tip.num }}</span>
        </div>
        <div class="no bg-radius text ta-center">
          <p>{{ language.label_3_1 }}</p>
          <div class="flex jc-around fs-18">
            <b v-for="idx in tip.level+1" :key="tip.level+'_'+idx">
              {{ epochFormatter(tip.nftIds[idx - 1]) }}
            </b>
          </div>
        </div>
        <div class="reward flex jc-center ai-center">
          <img :src="coinMap.DFT" alt="DFT" class="icon-m d-b">
          <p>
            {{ language.reward }}(DFT)<br>
            <b class="fs-20">{{ tip.rewardAmount }}</b>
          </p>
        </div>
      </li>
    </ul>
    <!--    <div v-show="loading" class="mask flex jc-center ai-center">-->
    <!--      <span class="ta-center">-->
    <!--        <i class="icon-loading"></i><br>{{ language.tip_loading }}...-->
    <!--      </span>-->
    <!--    </div>-->
  </div>
</template>

<script>
import {nftGraphql} from "@/common/api";
import {mapActions, mapGetters, mapState} from "vuex";
import {coinMap, prizeMap} from "@/common/walletConfig";
import {dateFormatter} from "@/common/dataConvertor";
import {BN} from "@/common/util";

export default {
  name: "lotteryReward",
  computed: {
    ...mapState('nft', ['market']),
    ...mapGetters(['language']),
  },
  data() {
    return {
      coinMap,
      prizeMap,
      showList: false,
      epochRounds: [],
      currentLottery: {},
      tips: [
        {level: 0, num: '—', rewardAmount: '——', nftIds: []},
        {level: 1, num: '—', rewardAmount: '——', nftIds: []},
        {level: 2, num: '—', rewardAmount: '——', nftIds: []},
      ],
      loading: false
    }
  },
  methods: {
    ...mapActions('nft', ['setMarket']),
    epochFormatter(epoch) {
      return epoch ? '#' + epoch : '——'
    },
    dateFormatter(str) {
      return str ? dateFormatter(str).format('yyyy-MM-dd') : '???-??-??'
    },
    getRoundList() {
      let query = `query{
        rounds(first: 20, orderBy: startBlock, orderDirection:desc) {
          startAt
          endAt
          epoch
          endBlock
          rewardAmount
          totalPoints
        }
      }`;
      nftGraphql(query).then(({rounds}) => {
        this.setMarket(rounds[0]);
        rounds.splice(0,1);
        this.currentLottery = rounds[0];
        this.epochRounds = rounds;
        this.getRewardList(rounds[0].epoch);
      });
    },
    getRewardList(epoch) {
      let query = `query{
          winNumbers(where: {round: "${epoch}"}) {
            id
            nftIds
            rewardAmount
            level
          }
        }`;
      nftGraphql(query).then(({winNumbers}) => {
        this.tips = winNumbers.map(el => {
          let {rewardAmount, level} = el;
          let num = level + 1;
          rewardAmount = new BN(rewardAmount).div(num).myFixed();
          return {...el, rewardAmount, num}
        })
      });
    },
    levelFormatter(lv) {
      let textMap = ['prize_1', 'prize_2', 'prize_3', 'prize_4', 'prize_5', 'prize_6'];
      return this.language[textMap[lv]]
    },
    onSelect(round) {
      this.currentLottery = round;
      this.getRewardList(round.epoch);
    }
  },
  beforeMount() {
    this.getRoundList();
  }
}
</script>

